<template>
    <div>
        <h2>Send WhatsApp Message</h2>
        <pre>{{ enDate }}</pre>
        <p>{{ projectKey }}</p>

        <div>
            <label for="phoneNumber">Telephone Number:</label>
            <input type="text" id="phoneNumber" v-model="phoneNumber">
        </div>
        <div>
            <label for="whatsappMessage">WhatsApp Message:</label>
            <textarea id="whatsappMessage" v-model="whatsappMessage"></textarea>
        </div>
        <button @click="sendMessage">Send WhatsApp Message</button>
    </div>
    <div>
        <div>
            <h3>{{ currentContact.name }}</h3>
            <p>{{ currentContact.phoneNumber }}</p>
            <p>{{ currentContact.message }}</p>
            <button @click="sendWhatsAppMessage">Send via WhatsApp</button>
            <button @click="startProcessing">Start</button>
            <button @click="reset">Reset</button>
            <input type="file" @change="loadCSVFile" accept=".csv">
            <button @click="exportToCSV">Export to CSV</button>
            <div>
                <label for="jumpIndex">Enter Index:</label>
                <input type="number" id="jumpIndex" v-model="jumpIndex">
                <button @click="jumpToIndex">Jump</button>
            </div>
            <p v-if="error" class="error">{{ error }}</p>
        </div>

        <h2>Contacts List</h2>
        <table>
            <thead>
                <tr>
                    <th>No</th>
                    <th>Name</th>
                    <th>Language</th>
                    <th>Phone Number</th>
                    <th>Message</th>
                    <th>Status</th>
                    <th>Date</th>
                    <th>na1</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(contact, index) in contacts" :key="index" :class="{ 'selected': currentIndex === index }">
                    <td>{{ index + 1 }}</td>
                    <td>{{ contact.name }}</td>
                    <td>{{ contact.language }}</td>
                    <td>{{ contact.phoneNumber }}</td>
                    <td>{{ contact.message }}</td>
                    <td>{{ contact.status }}</td>
                    <td>{{ contact.date }}</td>
                    <td>{{ contact.na1 }}</td>
                </tr>
            </tbody>
        </table>
        <DataTable :value="contacts" :paginator="true" :rows="10">
            <Column field="name" header="Name" :editable="true"></Column>
            <Column field="language" header="Language" :editable="true"></Column>
            <Column field="phoneNumber" header="Phone Number" :editable="true"></Column>
            <Column field="message" header="Message" :editable="true"></Column>
            <Column field="status" header="Status" :editable="true"></Column>
            <Column field="date" header="Date" :editable="true"></Column>
            <Column field="na1" header="na1" :editable="true"></Column>
        </DataTable>
    </div>
</template>

<script>
// import messageEn from './messageEn.json';
// import messageCn from './messageCn.json';
// import messageBs from './messageBs.json';

import { DataTable } from 'primevue/datatable';
import Column from 'primevue/column';
import Papa from 'papaparse';

export default {
    name: 'ContactList',
    props: {
        projectKey: {
            type: String,
            required: true
        },
        enDate: {
            type: String,
            required: true
        }
    },
    components: {
        DataTable,
        Column,
    },
    mounted() {
        this.loadMessageFiles();
    },
    data() {
        return {
            messageEn: [],
            messageCn: [],
            messageBs: [],
            phoneNumber: '',
            whatsappMessage: '',
            jumpIndex: null,
            contacts: [],
            currentIndex: 0,
            error: null
        };
    },
    computed: {
        currentContact() {
            return this.contacts[this.currentIndex] || {};
        }
    },
    methods: {
        loadMessageFiles() {
            this.loadTextFile('/messageEn.txt', 'messageEn');
            this.loadTextFile('/messageCn.txt', 'messageCn');
            this.loadTextFile('/messageBs.txt', 'messageBs');
        },
        loadTextFile(filePath, propertyName) {
            fetch(filePath)
                .then(response => response.text())
                .then(data => {
                    console.log(`File loaded successfully: ${filePath}`);
                    console.log(`Data: ${data}`);
                    const lines = data.split('\n');
                    const dataArray = [];
                    let currentObject = { code: '', msg: '' };


                    lines.forEach(line => {
                        if (line.startsWith('@')) {

                            if (currentObject.code !== '') {
                                dataArray.push(currentObject);
                            }
                            const [code, msg] = line.split('|');
                            currentObject = { code: code.trim().substring(1), msg: msg.trim() };
                        } else {
                            currentObject.msg += line.trim().replaceAll("$plink", "https://alvintkb.github.io/?p=gyp" + this.enDate + this.projectKey) + "\n";
                        }
                    });

                    if (currentObject.code !== '') {

                        dataArray.push(currentObject);
                    }

                    this[propertyName].splice(0, this[propertyName].length, ...dataArray);
                })
                .catch(error => {
                    console.error('Error loading text file:', error);
                });
        },
        sendMessage() {
            if (this.phoneNumber && this.whatsappMessage) {
                let msg = this.getMessage(this.whatsappMessage, "en");
                //    navigator.clipboard.writeText(msg);

                // Create a temporary textarea element to hold the text
                const textarea = document.createElement('textarea');
                textarea.value = msg;
                document.body.appendChild(textarea);

                // Select and copy the text
                textarea.select();
                document.execCommand('copy');

                // Remove the temporary textarea
                document.body.removeChild(textarea);


                const cleanedPhoneNumber = this.cleanPhoneNumber(this.phoneNumber);
                const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
                window.open(url, '_blank');
            } else {
                alert('Please enter both telephone number and WhatsApp message.');
            }
        },
        jumpToIndex() {
            if (this.jumpIndex >= 0 && this.jumpIndex < this.contacts.length) {
                this.currentIndex = this.jumpIndex - 1;
            } else {
                console.error("Invalid index.");
            }
        },
        sendWhatsAppMessage() {
            const { phoneNumber, message, language, na1 } = this.currentContact;
            if (!(language.includes("en") || language.includes("cn") || language.includes("bs"))) {
                alert('Language Error ' + language);
                return;
            }
            if (na1 !== 'w') {
                this.moveToNextContact();
                return;
            }
            if (this.validateInput(na1)) {
                let msg = this.getMessage(message, language);
                navigator.clipboard.writeText(msg);
                const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);
                const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;

                const textarea = document.createElement('textarea');
                textarea.value = msg;
                document.body.appendChild(textarea);
                textarea.select();
                textarea.setSelectionRange(0, textarea.value.length);
                document.execCommand('copy');
                document.body.removeChild(textarea);

                window.open(url, '_blank');

                this.moveToNextContact();
            }
        },
        getMessage(message, language) {
            let msg = "";

            if (language === "cn") {
                try {
                    msg = this.messageCn.find(msg => msg.code === message.slice(1)).msg;
                }
                catch (error) {
                    console.error("An error occurred on chinese source text:", error);
                }
            }
            else if (language === "bs") {
                try {
                    msg = this.messageBs.find(msg => msg.code === message.slice(1)).msg;
                }
                catch (error) {
                    console.error("An error occurred on English source text:", error);
                }
            }
            else {
                try {
                    msg = this.messageEn.find(msg => msg.code === message.slice(1)).msg;
                }

                catch (error) {
                    console.error("An error occurred on English source text:", error);
                }
            }
            return msg;
        },
        validateInput(message) {
            if (!message) {
                this.error = 'Please enter a message.';
                return false;
            }
            this.error = null;
            return true;
        },
        moveToNextContact() {
            let checkedContacts = 0;
            do {
                this.currentIndex++;
                if (this.currentIndex >= this.contacts.length) {
                    this.currentIndex = 0;
                    checkedContacts++;
                    if (checkedContacts >= this.contacts.length) {
                        break;
                    }
                }
            } while (this.contacts[this.currentIndex].na1 !== 'w');
        },


        startProcessing() {
            this.currentIndex = 0;
            let checkedContacts = 0;
            while (this.contacts[this.currentIndex].na1 !== 'w') {
                this.moveToNextContact();
                checkedContacts++;
                if (checkedContacts >= this.contacts.length) {
                    break;
                }
            }
        },
        reset() {
            this.currentIndex = 0;
        },
        loadCSVFile(event) {
            const file = event.target.files[0];
            if (!file) {
                this.error = 'Please select a file.';
                return;
            }
            Papa.parse(file, {
                header: true,
                complete: (results) => {
                    this.contacts = results.data.map(contact => ({
                        name: contact.name ? contact.name.trim() : '',
                        language: contact.language ? contact.language.trim() : '',
                        phoneNumber: contact.phoneNumber ? contact.phoneNumber.trim() : '',
                        message: contact.message ? contact.message.trim() : '',
                        status: contact.status ? contact.status.trim() : '',
                        date: contact.date ? contact.date.trim() : '',
                        na1: contact.na1 ? contact.na1.trim() : ''
                    }));
                },
                error: (error) => {
                    console.error('Error parsing CSV file:', error);
                    this.error = 'Error parsing CSV file.';
                }
            });
        },
        exportToCSV() {
            const csvContent = this.contacts.map(contact => {
                return `${contact.name},${contact.language},${contact.phoneNumber},${contact.message},${contact.status},${contact.date},${contact.na1}`;
            }).join('\n');

            const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
            const link = document.createElement('a');
            const url = URL.createObjectURL(blob);
            link.setAttribute('href', url);
            link.setAttribute('download', 'contacts.csv');
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
}
</script>

<style scoped>
table {
    width: 100%;
    border-collapse: collapse;
}

th,
td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    background-color: #f2f2f2;
}

.col-no {
    width: 50px;
}

.col-name {
    width: 150px;
}

.col-language {
    width: 100px;
}

.col-phone {
    width: 150px;
}

.col-message {
    width: 250px;
}

.col-status {
    width: 100px;
}

.col-date {
    width: 150px;
}

.col-na1 {
    width: 100px;
}
</style>
