<template>
    <div>
      <DataTable :value="products" :rowClass="rowClass" :rowStyle="rowStyle">
        <Column field="brand" header="Brand" :editable="true"></Column>
        <Column field="year" header="Year" :editable="true"></Column>
        <Column field="color" header="Color" :editable="true"></Column>
        <Column field="vin" header="Vin" :editable="true"></Column>
      </DataTable>
      <ColorPicker v-model="color" />
    </div>
  </template>
  <script>
  import DataTable from "primevue/datatable";
  import Column from "primevue/column";
  import ColorPicker from 'primevue/colorpicker';
  


  export default {
    name: "testTable",
    data() {
      return {
        products: [
          {"brand": "Volkswagen", "year": 2012, "color": "Orange", "vin": "dsad231ff"},
          {"brand": "Audi", "year": 2011, "color": "Black", "vin": "gwregre345"},
          {"brand": "Renault", "year": 2005, "color": "Gray", "vin": "h354htr"},
          {"brand": "BMW", "year": 2003, "color": "Blue", "vin": "j6w54qgh"},
          {"brand": "Mercedes", "year": 1995, "color": "Orange", "vin": "hrtwy34"},
          {"brand": "Volvo", "year": 2005, "color": "Black", "vin": "jejtyj"},
          {"brand": "Honda", "year": 2012, "color": "Yellow", "vin": "g43gr"},
          {"brand": "Jaguar", "year": 2013, "color": "Orange", "vin": "greg34"},
          {"brand": "Ford", "year": 2000, "color": "Black", "vin": "h54hw5"},
          {"brand": "Fiat", "year": 2013, "color": "Red", "vin": "245t2s"}
        ],
        color: '1976D2',
        highlightedRow: 2 // Row 3 (zero-based index)
      };
    },
    components: {
      DataTable,
      Column,
      ColorPicker
    },
    methods: {
     rowClass : (data) => {
    return [{ 'bg-gray-100': data.brand !== 'Audi' }];
},


rowStyle : (data) => {
    if (data.year === 2012) {
        return { color: 'white', background:'red' };
    }}}};
     
    

    
 

  

  </script>
  
  <style scoped>
  /* Add scoped styles here if needed */
  </style>
  