import { createApp, provide, h } from 'vue';
import { ApolloClient, InMemoryCache, createHttpLink } from '@apollo/client/core';
import { DefaultApolloClient } from '@vue/apollo-composable';

import PrimeVue from 'primevue/config';
import DataTable from 'primevue/datatable';
// eslint-disable-next-line vue/multi-word-component-names
import Column from 'primevue/column';
import ColorPicker from 'primevue/colorpicker';
import InputText from 'primevue/inputtext';

import App from './App.vue';

// Create the HTTP Link to your GraphQL endpoint
const httpLink = createHttpLink({
  uri: 'https://sp412b-763ef468411d.herokuapp.com/graphql',
});

// Create the Apollo Client instance
const apolloClient = new ApolloClient({
  link: httpLink,
  cache: new InMemoryCache(),
});

// Create the Vue app instance
const app = createApp({
  setup() {
    provide(DefaultApolloClient, apolloClient);
  },
  render: () => h(App),
});

app.config.globalProperties.cleanPhoneNumber = function(phoneNumber) {
  return phoneNumber.replace(/[\s-]/g, '');
}

app.use(PrimeVue);
app.component('DataTable', DataTable);
// eslint-disable-next-line vue/multi-word-component-names
app.component('Column', Column);
app.component('ColorPicker', ColorPicker);
app.component('InputText', InputText);
// Mount the Vue app to the #app element in the DOM
app.mount('#app');


